$component: --mt-quadro-03;

:host {
  #{$component}-container-padding: 12px;
  #{$component}-container-border: 2px solid var(--mt-color-quadros-linha-01);

  #{$component}-title-color: var(--mt-color-quadros-fonte-02);
  #{$component}-title-font-family: 'Noto Sans', sans-serif;
  #{$component}-title-font-weight: 700;
  #{$component}-title-text-transform: none;
  #{$component}-title-font-size: 16px;
  #{$component}-title-margin: 0 0 12px 0;

  #{$component}-text-color: var(--mt-color-quadros-fonte-02);
  #{$component}-text-font-family: 'Noto Sans', sans-serif;
  #{$component}-text-font-weight: 400;
  #{$component}-text-text-transform: none;
  #{$component}-text-font-size: 16px;

  #{$component}-list-color: var(--mt-color-quadros-fonte-02);
  #{$component}-list-font-family: 'Noto Sans', sans-serif;
  #{$component}-list-font-weight: 400;
  #{$component}-list-text-transform: none;
  #{$component}-list-font-size: 16px;
  #{$component}-list-margin: 12px 0 0 0;
  #{$component}-list-padding: 0 0 0 20px;

  display: block;
}

.mt-quadro-03 {
  &__container {
    padding: var(#{$component}-container-padding);
    border: var(#{$component}-container-border);
  }

  &__title {
    color: var(#{$component}-title-color);
    font-family: var(#{$component}-title-font-family);
    font-weight: var(#{$component}-title-font-weight);
    text-transform: var(#{$component}-title-text-transform);
    font-size: var(#{$component}-title-font-size);
    line-height: 1;
    margin: var(#{$component}-title-margin);
  }

  &__text {
    color: var(#{$component}-text-color);
    font-family: var(#{$component}-text-font-family);
    font-weight: var(#{$component}-text-font-weight);
    text-transform: var(#{$component}-text-text-transform);
    font-size: var(#{$component}-text-font-size);
    line-height: 1;
    margin: 0;
  }
}

::slotted([slot='list']) {
  color: var(#{$component}-list-color);
  font-family: var(#{$component}-list-font-family);
  font-weight: var(#{$component}-list-font-weight);
  text-transform: var(#{$component}-list-text-transform);
  font-size: var(#{$component}-list-font-size);
  line-height: 1;
  margin: var(#{$component}-list-margin);
  padding: var(#{$component}-list-padding);
}
